@import url(~purecss/build/pure.css);
@import url(~purecss/build/grids.css);
@import url(~purecss/build/grids-responsive.css);
@import url(~purecss/build/menus.css);
.App {
  margin: 1em;
  text-align: center; }

@media (max-width: 420px) {
  .App {
    margin: 0; }
  .pure-menu-link {
    padding: 0.5em; } }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 1.5em;
  /*position: fixed;
  top: 2em;
  left: 2em;*/
  margin: 1em;
  vertical-align: middle; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.truncate, .AppHeader .blogLinks .blogPostLink {
  width: 250px;
  /*white-space: nowrap;*/
  /*overflow: hidden;*/
  /*text-overflow: ellipsis;*/ }

.AppHeader {
  padding: 2px;
  margin-bottom: 2em;
  background-color: #eee;
  border: solid 2px black;
  font-weight: bold; }
  .AppHeader .pure-menu-link {
    color: #222; }
  .AppHeader .pure-menu-children {
    border: 2px solid #ddd;
    background-color: #FDFDFD;
    -webkit-transform: translate(-50%, 0);
        -ms-transform: translate(-50%, 0);
            transform: translate(-50%, 0); }
    .AppHeader .pure-menu-children .pure-menu-link {
      color: #444; }
      .AppHeader .pure-menu-children .pure-menu-link:hover {
        background-color: #eee; }
  .AppHeader .blogLinks li a {
    white-space: normal; }
  .AppHeader .blogLinks .blogPostLink {
    text-align: left;
    font-size: .9em; }
    .AppHeader .blogLinks .blogPostLink span {
      font-size: .6em; }
  .AppHeader .contactLinks img {
    height: 1em;
    vertical-align: middle;
    margin: 0 0.2em; }
  .AppHeader .contactLinks .linkedin {
    color: #007bb6; }
  .AppHeader .contactLinks .github {
    color: black; }
  .AppHeader .contactLinks .twitter {
    color: #00aced; }

.AppContent section {
  margin-bottom: 3em; }
  .AppContent section p {
    font-size: 1.2em;
    font-family: Alegreya, Palatino, Georgia, serif; }

.AppContent .expandButton {
  border: solid 2px black;
  background-color: #eee;
  padding: 1em 2em;
  position: relative;
  left: 0;
  right: 0;
  margin: 0 auto;
  margin-bottom: 1em; }

.AppFooter {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #EEE;
  padding: 0.5em 0;
  /*font-size: .8em;*/
  color: #777; }

svg text {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

svg text::-moz-selection {
  background: none; }

svg text::selection {
  background: none; }

.vx-axis-left line {
  opacity: 0; }

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@-webkit-keyframes fade-in-down {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-10px);
    transform: translateY(-10px); }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0); } }

@keyframes fade-in-down {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-10px);
    transform: translateY(-10px); }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0); } }

.banner a h1 {
  -webkit-animation: fade-in-down 0.6s both;
          animation: fade-in-down 0.6s both;
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s; }



